.cat-info {
  column-gap: 20px;
  margin-top: 20px;
  display: flex;
}

.cat-description {
  max-width: 600px;
}

.loader-icon.is-hidden, .breed-select.is-hidden, .loader.is-hidden, .error.is-hidden {
  display: none;
}

.error {
  color: red;
}

.breed-select {
  max-width: 500px;
  color: #fff;
  background-color: rgba(128, 128, 128, .9);
}

.loader-icon {
  content: "";
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

